export default class extends React.Component {
  static async getInitialProps({ query }) {
    let idClip = query.id;

    let reqAudio = await fetch(
      `https://api.audioboom.com/audio_clips/${idClip}.mp3`
    );
    let dataAudios = await reqAudio.json();
    let Clip = dataAudios.body.audio_clip;

    return { Clip };
  }

  render() {
    const { Clip } = this.props;
    return (
      <div>
        <header>Podcasts</header>
      

        <div className="clip">
          <nav>
            <a href="">{"< Volver"}</a>
          </nav>
          {/* <h2>Podcasts</h2> */}
          {/* <div className=""> */}
          <picture>
            <div>
              <img src={Clip.urls.image} alt="" />
            </div>
            {/* </div> */}

            <h3>{Clip.title}</h3>
          </picture>
        </div>

        <style jsx>{`
          nav {
            background: none;
          }
          nav a {
            display: inline-block;
            padding: 15px;
            color: white;
            cursor: pointer;
            text-decoration: none;
          }
          .clip {
            display: flex;
            height: 100%;
            flex-direction: column;
            background: #8756ca;
            color: white;
          }
          picture {
            display: flex;
            align-items: center;
            justify-content: center;
            flex: 1 1;
            flex-direction: column;
            width: auto;
            padding: 10%;
          }
          picture div {
            width: 100%;
            height: 100%;
            background-position: 50% 50%;
            background-size: contain;
            background-repeat: no-repeat;
          }
          .player {
            padding: 30px;
            background: rgba(0, 0, 0, 0.3);
            text-align: center;
          }
          h3 {
            margin: 0;
          }
          h6 {
            margin: 0;
            margin-top: 1em;
          }
          audio {
            margin-top: 2em;
            width: 100%;
          }

          .modal {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 99999;
          }
        `}</style>

        <style jsx global>{`
          body {
            margin: 0;
            font-family: system-ui;
            background: white;
          }
        `}</style>
      </div>
    );
  }
}
